a {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
.item{
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-top: 10vh;
  max-width: 1400px;
  margin: auto;
  flex-wrap: wrap;
}
.price-item{
  text-align: center;
  color: rgb(99, 97, 97);
  text-transform: uppercase;
  font-weight:bold;
  font-size: 18px;
}

.cardItem-item{
  width: 300px;
  background-color: white;
  height: auto;
  margin: 10px;
  box-shadow:
0 2.8px 2.2px rgba(0, 0, 0, 0.044),
0 6.7px 5.3px rgba(0, 0, 0, 0.058),
0 12.5px 10px rgba(0, 0, 0, 0.07),
0 22.3px 17.9px rgba(0, 0, 0, 0.082),
0 41.8px 33.4px rgba(0, 0, 0, 0.096),
0 100px 80px rgba(0, 0, 0, 0.12);   
}

.img{
    width: 100%;
    min-height: auto;
}

.rotate-hor-center:hover {
-webkit-animation: rotate-hor-center 0.5s cubic-bezier(0.455, 0.030, 0.515, 0.955) both;
  animation: rotate-hor-center 0.5s cubic-bezier(0.455, 0.030, 0.515, 0.955) both;
  opacity: 0.5;
}

.title-item{
  color:gray;
  text-transform: uppercase;
}

