
.noItems{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 200px;
    margin-top:200px ;
}

.textNoItems{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    text-transform: uppercase;
    color: grey;
}


.title-item-detail{
    color:gray;
    text-transform: uppercase;
    font-size: 50px;
    margin-top: 200px;
    margin-bottom: 100px;
}

.name {
    padding: 20px;
    font-size: 20px;
    text-transform: uppercase;
}

.number {
    color: gray;
}


/* boton */

    
.btn-detail-card {
    padding: 5px;
    width: 150px;
    height: 75px;
    margin-top: 40px;
    margin-left: 0;
    background-color: #075d97ab;
    color: white;
    border-radius: 15px;
    font-weight: bold;
    border:rgb(99, 97, 97);
    text-transform: uppercase;
    box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.044),
    0 6.7px 5.3px rgba(0, 0, 0, 0.058),
    0 12.5px 10px rgba(0, 0, 0, 0.07),
    0 22.3px 17.9px rgba(0, 0, 0, 0.082),
    0 41.8px 33.4px rgba(0, 0, 0, 0.096),
    0 100px 80px rgba(0, 0, 0, 0.12);  

}

.links-bottom{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.links-card {
    margin-right: 50px;
    margin-left: 50px;

}